    created () {
      this.handleUserList()
    },
    methods: {
      // 初始页currentPage、初始每页数据数pagesize和数据data
      handleSizeChange: function (size) {
        this.pagesize = size
        // 每页下拉显示数据
        // console.log(this.pagesize)
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage
        // 点击第几页
        // console.log(this.currentPage)
      },
      handleUserList () {
        this.userList = this.userList
      },
      // &event是实参，表示event对象
      get: function(ev) {
        // 如果按得键是上或者下，就不进行ajax
        if (ev.keyCode == 38 || ev.keyCode == 40) {
          return;
        }
        this.$http.jsonp('https://www.baidu.com/s?ie=utf-8&f=8&rsv_bp=0&rsv_idx=1&tn=baidu&wd=' + this.searchString).then(function(res) {
          this.myData = res.data.s;
        });

      },
      selectDown: function() {
        this.now++;
        //到达最后一个时，再按下就回到第一个
        if (this.now == this.myData.length) {
          this.now = 0;
        }
        this.searchString = this.myData[this.now];
      },
      selectUp: function() {
        this.now--;
        //同上
        if (this.now == -1) {
          this.now = this.myData.length - 1;
        }
        this.searchString = this.myData[this.now];
      },
      search: function() {
        //打开对应的搜索界面
        window.open(this.logoData[this.searchIndex].searchSrc +encodeURIComponent( this.searchString));
      },
      selectHover: function(index) {
        this.now = index
      },
      selectClick: function(index) {
        this.searchString = this.myData[index];
        this.search();
      },
      clearInput: function() {
        this.searchString = '';
        this.$http.jsonp('https://www.baidu.com/s?ie=utf-8&f=8&rsv_bp=0&rsv_idx=1&tn=baidu&wd=' + this.searchString ).then(function(res) {
          this.myData = res.data.s;
        });
        // this.$http.jsonp('https://sug.so.360.cn/suggest?word=' + this.searchString + '&encodein=utf-8&encodeout=utf-8').then(function(res) {
        //   this.myData = res.data.s;
        // });
      },
      getIndex: function(index) {
        this.searchIndex = index;
      }
    },
    computed: {
      // 计算数学，匹配搜索
      filteredArticles: function () {
        var articles_array = this.userList,
          searchString = this.searchString;

        if(!searchString){
          return articles_array;
        }

        searchString = searchString.trim().toLowerCase();

        articles_array = articles_array.filter(function(item){
          if(item.title.toLowerCase().indexOf(searchString) !== -1 && searchString!==''){
            return item;
          }
        })

        // 返回过来后的数组
        return articles_array;
      }
    }
  }
</script>
